  .wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    -webkit-transition: margin .5s;
    transition: margin .5s;
    margin: 0 0 0 -250px;
  }

  .wrepper.is-nav-open {
    margin-left: 0;
  }

  .nav {
    position: relative;
    width: 250px;
    height: 100%;
    padding: 20px;
    border-right: 1px solod #CCC; 
  }

  .nav__icon {
    position: absolute;
    top: 0;
    right: -60px;
    padding: 20px;
    font-size: 20px;
    cursor: pointer;
    -webkit-transition: color .3s;
    transition: color .3s
  }

  .nav__icon:hover{
    color: #5eb2ff;
  }

